body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  z-index: 1000;
  background-color: #3498db;
  width: 100%;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px #0000001a;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 70px;
  display: flex;
}

.photo-card {
  border-radius: 8px;
  width: calc(25% - 20px);
  margin: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #0000001a;
}

.photo-card img {
  object-fit: cover;
  border-radius: 8px 8px 0 0;
  width: 100%;
  height: 400px;
}

.info {
  text-align: center;
  background-color: #ffffffe6;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  font-size: 12px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px;
  transition: background-color .3s;
  display: block;
}

.load-more:hover {
  background-color: #2980b9;
}
